	const videoSet = {
		swipe: false,
		dots: true,
		infinite: true,
		speed: 2000,
		fade: true,
		arrows:false,
		slidesToShow: 1,
		slidesToScroll: 1,
		autoplay: true,
		autoplaySpeed: 13000,
		pauseOnHover: false,
		pauseOnFocus: false,
		pauseOnDotsHover: false,
		dotsClass: "progress",
		className: 'customName', // <== your custom name
		//appendDots:$(".progress"),// 이게 안됨 ㅠ
		appendDots: dots => {
			return (
			  <div>
			    <ul className="slick-dots">
			      {dots.map((item, index) => {
					return (
						<li key={index} className={item.props.className}>{item.props.children}</li>
					);
			      })}
			    </ul>
			  </div>
			)
		      },
		/* customPaging: function(slide, i) {
			return (`<div class="text">${i}</div>`)
		} */
		customPaging: index => {
			return ( //style={index === this.state.currentSlide ? testSett: null }
				<div className="text" >
				{index + 1}
				</div>
			)
		},
		beforeChange: (current, next) => {
			//this.setState({ activeSlide: next });
			$('.player').each(function(i) {
				var video = $(this).get(0);
				video.currentTime = 0; // 영상 처음으로 설정
				video.pause();
				video.play();
			});
		},
		//afterChange: current => this.setState({ activeSlide2: current })
	};

